import React, { useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@mui/material';

interface TableData {
  id: number;
  start_date: string;
  end_date: string;
  table_extract: string;
  graph_extract: string;
  node_extract: string;
  created_at : string;

  // Add more properties as needed
}

const TableReportHistory: React.FC = () => {
  const [rows, setData] = useState<TableData[]>([    {
    created_at: " ",
    id: 0,
    start_date: " ",
    end_date: " ",
    table_extract: " ",
    graph_extract: " ",
    node_extract: " ",
}]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://0.0.0.0:8080/export/getListReport');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        const result = await response.json();
        setData(result);
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
      }
    };

    fetchData();
  }, []);

  return (
    <div>
      <Typography variant='h6' fontWeight='bold'> History report created : </Typography>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>Node Extract</TableCell>
            <TableCell>Start Date</TableCell>
            <TableCell>End Date</TableCell>
            <TableCell>Table Extract</TableCell>
            <TableCell>Graph Extract</TableCell>
            <TableCell>Created At</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow>
              <TableCell>{row.id}</TableCell>
              <TableCell>{row.node_extract}</TableCell>
              <TableCell>{row.start_date}</TableCell>
              <TableCell>{row.end_date}</TableCell>
              <TableCell>{row.table_extract}</TableCell>
              <TableCell>{row.graph_extract}</TableCell>
              <TableCell>{row.created_at}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
};

export default TableReportHistory;