import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';

import Link from '@mui/material/Link';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {DateRangeForm} from './DateRangeForm';
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';
import {TableCheckboxesGroup} from './TableCheckboxesGroup';
import {GraphCheckboxesGroup} from './GraphCheckboxesGroup';
import TableReportHistory from './TableReportHistory';
import { Grid } from '@mui/material';
import DropdownSelectWithCheckboxes from './DropdownSelectWithCheckboxes';
import { useState } from 'react';

export interface Node {
  instance: string;
  node_name: string;
}

interface FormData {
  // Define the properties of your JSON body
  start_date: string;
  end_date: string;
  table_field: string[];
  graph_field: string[];
  node_field: Node[];
}

function Copyright(props: any) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <Link color="inherit" href="https://lintasarta.co.id/">
        Lintasarta
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

// TODO remove, this demo shouldn't need to reset the theme.
const defaultTheme = createTheme();

export default function FormCreateReport() {
  const [selectedNodes, setSelectedNodes] = useState<Node[]>([]);
  const [selectedTable, setSelectedTable] = useState<string[]>([]);
  const [selectedGraph, setSelectedGraph] = useState<string[]>([]);
  const [selectedStartDate, setSelectedStartDate] = useState<Date | null>(null);
  const [selectedEndDate, setSelectedEndDate] = useState<Date | null >(null);


  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    var start_date = '';
    var end_date = '';
    if (selectedStartDate){
      start_date = selectedStartDate.toISOString()
    }
    if (selectedEndDate){
      end_date = selectedEndDate.toISOString()
    }
    const data : FormData = {
      table_field: selectedTable,
      graph_field: selectedGraph,
      node_field: selectedNodes,
      start_date: start_date,
      end_date: end_date,
    }
    try {
      // Make a network request to your server
      const response = await fetch('http://0.0.0.0:8080/export/createReport', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        // Include any necessary data in the body of the request
        body: JSON.stringify({data}),
      });

      if (!response.ok) {
        throw new Error('Network response was not ok');
      }

      // Assuming the server responds with the appropriate headers for a file download
      const blob = await response.blob();

      // Create a temporary URL for the Blob
      const url = URL.createObjectURL(blob);

      // Create a link element and trigger a download
      const link = document.createElement('a');
      link.href = url;
      link.download = 'Report.pdf'; // Set the desired filename
      document.body.appendChild(link);
      link.click();

      // Clean up
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

      // Continue with further actions after the download
      console.log('File downloaded successfully!');
      window.location.reload();

    } catch (error) {
      console.error('Error downloading file:', error);
    }
  };

  return (
    <Container
    maxWidth="xl" // Adjust the maxWidth based on your design
    style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      minHeight: '100vh',
    }}
  >
    <Grid container spacing={2}>

    <ThemeProvider theme={defaultTheme}>
    <Grid item maxWidth='xs'>

      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 16,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <ReceiptLongIcon />    
          </Avatar>
          <Typography component="h1" variant="h5">
            Create Report
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <DropdownSelectWithCheckboxes labelId={'node_field'} setSelected={setSelectedNodes} />
            <DateRangeForm selectedStartDate={selectedStartDate} setSelectedStartDate={setSelectedStartDate} selectedEndDate={selectedEndDate} setSelectedEndDate={setSelectedEndDate}/>
            <TableCheckboxesGroup setSelectedTable={setSelectedTable}/>
            <GraphCheckboxesGroup setSelectedGraph={setSelectedGraph}/>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ marginTop : '16px' }}
            >   
              Create
            </Button>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />
      </Container>
      </Grid>

      <Grid item sm={8}>
        <Box
            sx={{
                marginTop: 16,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
            }}
            >
            <TableReportHistory/>
        </Box>
      </Grid>
    </ThemeProvider>
    </Grid>
    </Container>
  );
}