import React, { useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableHead, TableRow, Typography } from '@mui/material';

interface TableData {
  id: number;
  report_type: string;
  email_list: string;
  table_extract: string;
  graph_extract: string;
  node_extract: string;

}

const TableJobHistory: React.FC = () => {
  const [rows, setData] = useState<TableData[]>([    {
    id: 0,
    report_type: " ",
    email_list: " " ,
    table_extract: " ",
    graph_extract: " ",
    node_extract: " "
}]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://0.0.0.0:8080/export/getListJob');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        const result = await response.json();
        setData(result);
      } catch (error) {
        console.error('Error fetching data:', error);
      } finally {
      }
    };

    fetchData();
  }, []);

  return (
    <div>
      <Typography variant='h6' fontWeight='bold'>Job created list :</Typography>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>Node Extract</TableCell>
            <TableCell>Report Type</TableCell>
            <TableCell>Table Extract</TableCell>
            <TableCell>Graph Extract</TableCell>
            <TableCell>Email List</TableCell>

          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow>
              <TableCell>{row.id}</TableCell>
              <TableCell>{row.node_extract}</TableCell>
              <TableCell>{row.report_type}</TableCell>
              <TableCell>{row.table_extract}</TableCell>
              <TableCell>{row.graph_extract}</TableCell>
              <TableCell>{row.email_list}</TableCell>

            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
};

export default TableJobHistory;